.offer-room {
    position: relative;
    >input{
        position: absolute;
        inset: 0;
        opacity: 0;
        z-index: 2;
        cursor: pointer;
    }
    &__check {
        position: absolute;
        top: -6px;
        right: -6px;
        width: 26px;
        height: 26px;
        background-color: var(--blue);
        border-radius: 50%;

        display: flex;
        align-items: center;
        justify-content: center;

        opacity: 0;
        visibility: hidden;

        transition: opacity $transitionSpeed $transitionFunction,
            visibility $transitionSpeed $transitionFunction;

            z-index: 3;
        svg {
            width: 13px;
            height: 13px;
            fill: none;
            stroke: var(--white);
            stroke-width: 1.5px;
        }
    }

    &__container {
    }

    &__image {
        padding-bottom: percent(248, 366);
        border-radius: 12px;
        overflow: hidden;
        position: relative;
       img{
        transition: opacity $transitionSpeed $transitionFunction;
       }
       &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 35%;
        z-index: 1;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.78) 100%);
    }
    }

    &__title {
        position: absolute;
        bottom: 16px;
        left: 16px;

        color: var(--white);
        z-index: 2;
        max-width: 100%;
        pointer-events: none;
    }

    &__subtitle {
        margin-top: 12px;
        color: var(--dark);
        font-size: var(--small-fz);
        line-height: var(--small-lh);
        font-weight: 500;
        span{
            display: block;
        }
    }
    &__price{
    }
    &._active {
        .offer-room__check {
            opacity: 1;
            visibility: visible;
        }
    }
    &--border{
        border: 1px solid var(--primary200);
        border-radius: 12px;
        padding: 16px;
        transition: border $transitionSpeed $transitionFunction;
        .offer-room{
            &__image{
                padding-bottom: 0;
                &::before{
                    content: none;
                }
                img{
                    position: static;
                }
            }
            &__title{
                position: static;
                color: var(--dark);
                &:not(:last-child){
                    margin-bottom: 16px;
                }
            }
            &__check{
                width: 22px;
                height: 22px;
            }
        }
        &._active{
            border-color: var(--blue);
        }
    }
    &--border-img-up{
        .offer-room__image{
            margin-bottom: 16px;

        }
    }
    @media (min-width:$desktop-min){
        &:not(.offer-room--border) {
            &:hover{
                .offer-room__image img{
                    opacity: 0.8;
                }
             } 
        }
    }
}

