.title-1 {
    font-weight: 500;
    font-size: var(--very-big-fz);
    line-height: var(--very-big-lh);
    color: var(--dark);
}

.title-2 {
    font-weight: 500;
    font-size: var(--big-fz);
    line-height: var(--big-lh);
    color: var(--dark);
}

.title-3 {
    font-weight: 500;
    font-size: var(--little-big-fz);
    line-height: var(--little-big-lh);
    color: var(--dark);
}

.title-4 {
    font-weight: 500;
    font-size: var(--default-fz);
    line-height: var(--default-lh);
    color: var(--dark);
}

.text-center {
    text-align: center;
}
._disabled{
    pointer-events: none;
}


@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeTransformX {
    0% {
        transform: translateX(6%);
        opacity: 0;
    }

    100% {
        transform: translate(0);
        opacity: 1;
    }
}

@keyframes updown {
    25% {
        transform: translateY(-6px);
    }

    50% {
        transform: translateY(-12px);
    }

    75% {
        transform: translateY(-6px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes leftRightSmall {
    33% {
        transform: translateY(-2px);
    }

    66% {
        transform: translateY(2px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes leftRight {
    33% {
        transform: translateX(-10px);
    }

    66% {
        transform: translateX(10px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes fadeTransformY {
    0% {
        transform: translateY(6%);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes shake_online-display {

    10%,
    90% {
        transform: rotate(-90deg) translate3d(0, -1px, 0);
    }

    20%,
    80% {
        transform: rotate(-90deg) translate3d(0, 2px, 0);
    }

    30%,
    50%,
    70% {
        transform: rotate(-90deg) translate3d(0, -4px, 0);
    }

    40%,
    60% {
        transform: rotate(-90deg) translate3d(0, 4px, 0);
    }
}

@keyframes webSkeleton {
    to {
        transform: translateX(100%)
    }
}